.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

.movies .movie{
    width: 45%;
    background-color: #fff;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    font-weight: 300;
    padding: 20px;
    color: #b2b3bc;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.movies img{
    position: relative;
    top: -50px;
    max-width: 180px;
    width: 100%;
    margin-right: 30px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.movie .movie__title, .movie .movie__year{
    margin: 0;
    font-weight: 300;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__year,
.movie .movie_genre{
    list-style: none;
    padding: 0;
    display: flex;
    margin: 0 5px;
}